.card {
  border-radius: 16px;
  width: 360px;
  height: 360px;
  /* background: url(https://picsum.photos/360/360?image=0); */
  position: relative;
  overflow: hidden;
  box-shadow: 0 5px 50px rgba(0, 0, 0, 0.85);
}
.card:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  background-color: rgba(0, 0, 0, 0.92);
}
.card .img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
}
.card .img span {
  width: 25%;
  height: 100%;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  transition: 0.5s;
}
.card .img span:nth-child(1) {
  background-position: 0;
  transition-delay: 0;
}
.card .img span:nth-child(2) {
  background-position: 33.33333%;
  transition-delay: 0.1s;
}
.card .img span:nth-child(3) {
  background-position: 66.66666%;
  transition-delay: 0.2s;
}
.card .img span:nth-child(4) {
  background-position: 100%;
  transition-delay: 0.3s;
}

.card:hover .img > span {
  transform: translateY(-100%);
}
.card:hover .content {
  transform: translateY(0%);
  transition: 1s;
  transition-delay: 0.1s;
}

.content {
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: white;
  padding: 20px 20px;
  width: 100%;
  height: 100%;
  transform: translateY(100%);
}

.grid-card {
  position: relative;
  background-color: #70cff3;
  border-radius: 5px;
  /* width: 25vw;
  height: 25vw; */
}

.card {
  background-color: #009dd9 !important;
  border-radius: 0;
  width: 100%;
  height: auto;
}
.card::before {
  background-color: #009dd9 !important;
  width: 100%;
  height: auto;
  /* height: 250px !important; */
}
.grid-card::after {
  content: "";
  background: -webkit-linear-gradient(59deg, #3a6073, #16222a);
  /* width: 250px;
  height: 250px; */
  position: absolute;
  top: 0;
  left: 0;
  transform: rotateY(0) perspective(100px);
  transition: 0.4s;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}
.grid-img {
  object-fit: cover;
  overflow: hidden;
  object-position: center;
  transform: rotateY(180deg) perspective(100px);
  transition: 0.4s;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  padding: 1px;
  background-color: #70cff3;
  border-radius: 5px;
}

@media screen and (min-width: 400px) {
  .grid-img {
    padding: 4px;
  }
}

.grid-card-show::after {
  transform: rotateY(180deg) perspective(100px);
  /* width: 250px;
  height: 250px; */
}
.grid-card-show .grid-img {
  transform: rotateY(0) perspective(100px);
  /* width: 250px;
  height: 250px; */
}
.grid-card-finished {
  opacity: 0.5;
}

