.game-container {
  width: 345px;
  height: 445px;
  background: #ececec;
  outline: 1px black solid;
}
.game-header {
  /* width: 100%; */
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 20px;
}
.slidemoves {
  width: 150px;
  height: 100px;
  padding: 5px;
  padding-top: 40px;
  position: relative;
  line-height: 50px;
  font-size: 36px;
  font-weight: bold;
}
.slidemoves:before {
  content: "MOVES";
  display: block;
  position: absolute;
  top: 20px;
  left: 5px;
  height: 30px;
  line-height: 30px;
  font-size: 20px;
}
.big-button {
  display: block;
  padding: 10px 20px;
  text-align: center;
  height: 50px;
  line-height: 30px;
  font-size: 30px;
  font-weight: bold;
  background: white;
  border-radius: 5px;
  box-shadow: 0 0 10px #0002;
}
.slideboard {
  width: 345px;
  height: 345px;
  position: relative;
  background: #ddd;
}
.slideoverlay {
  width: 345px;
  height: 345px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
  background: #0004;
  display: flex;
  align-items: center;
  justify-content: center;
}
.slidetile {
  width: 80px;
  height: 80px;
  position: absolute;
  /* background: white; */
  transition: all 0.1s ease-in-out;
  border-radius: 2px;

  background-size: 400px 400px;
}

.puzzleframe {
  width: 345px;
  height: 345px;
  background: transparent;
  display: grid;
  grid-template-columns: repeat(9, 1fr);
  grid-template-rows: repeat(12, 1fr);
}
