#cube .front {
  transform: translateZ(110px) translateX(55px) translateY(120px);
}

#cube .back {
  transform: rotateX(-180deg) translateZ(110px) translateX(55px)
    translateY(-115px);
}

#cube .right {
  transform: rotateY(90deg) translateZ(170px) translateY(100px);
}

#cube .left {
  transform: rotateY(-90deg) translateZ(60px) translateY(125px);
}

#cube .top {
  transform: rotateX(90deg) translateZ(-5px) translateX(45px);
}

#cube .bottom {
  transform: rotateX(-90deg) translateZ(235px) translateX(60px);
}

.cubecontainer {
  width: 100px;
  height: 100px;
  position: relative;
  margin: 0 auto 0;
  /* border: px solid #fff; */

  perspective: 1000px;
  perspective-origin: 50% 100%;
}

#cube {
  width: 100%;
  height: 100%;
  top: 300px;
  left: -100px;
  position: absolute;

  transform-style: preserve-3d;

  transition: transform 5s;
}

#cube:hover {
  cursor: pointer;
}

#cube .bob {
  /* background: hsla(0, 85%, 50%, 0.8); */
  display: block;
  position: absolute;
  width: 200px;
  height: 100px;
  border: solid #f22613;
  box-shadow: 1px 0 0 rgba(255, 255, 255, 0.25);
  border-width: 1px 1px 1px 1px;
  /* border: 1px solid #f22613; */

  margin: 0 auto;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 500%;
  text-align: center;
  padding: 50px 0;
}
html,
body {
  height: 100%;
}

body {
  background-color: black;
}

* {
  box-sizing: border-box;
}
.boom {
  -webkit-transform-style: preserve-3d;
  transform-style: preserve-3d;
  -webkit-animation: antispin 15s infinite linear;
  animation: antispin 15s infinite linear;
}
.heart3d {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  width: 300px;
  height: 425px;
  -webkit-transform-style: preserve-3d;
  transform-style: preserve-3d;
  -webkit-animation: spin 15s infinite linear;
  animation: spin 15s infinite linear;
}
.heart3d [class^="rib"] {
  position: absolute;
  opacity: 0;
  width: 300px;
  height: 425px;
  border-width: 1px 1px 0 0;
  border-radius: 50% 50% 0 / 40% 50% 0;
}
.heart3d [class$="1"] {
  -webkit-animation: appear 1s 0.1s infinite alternate linear;
  animation: appear 1s 0.1s infinite alternate linear;
  -webkit-transform: rotateY(10deg) rotateZ(45deg) translateX(62px);
  transform: rotateY(10deg) rotateZ(45deg) translateX(62px);
